import React  from "react";
import useTheme from '../hooks/useTheme';
import {
  Navbar,
  NavbarBrand,
} from "shards-react";

const NavBar = () => {
  const [theme] = useTheme();

  return (
    <Navbar className="navbar" type="dark" theme={theme} expand="md" sticky="true">
      {/* <a href="#" className="logo"><img src="./logo.png"/></a>  */}
      <NavbarBrand className="navbar-brand">Love from Heaven</NavbarBrand>
    </Navbar>
  );
}

export default NavBar;


